<template>
  <div class="questions_select_body">
    <!-- 提示按钮 -->
    <div class="btn_wrapper">
      <span>说明：目前支持学科和关键字条件筛选</span>
      <el-button
        type="success"
        size="small"
        icon="el-icon-edit"
        @click="$router.push('/questions/new')"
        >新增试题</el-button
      >
    </div>
    <!-- 表单 -->
    <el-form
      :model="form"
      size="small"
      ref="form"
      class="select-form"
      label-width="80px"
    >
      <el-row>
        <el-col :span="6">
          <el-form-item label="学科" label-width="80px" prop="subjectID">
            <el-select v-model="form.subjectID" placeholder="请选择">
              <el-option
                v-for="subject in subjectList"
                :key="subject.id"
                :label="subject.subjectName"
                :value="subject.id"
              ></el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item label="二级目录" prop="catalogID">
            <el-select v-model="form.catalogID" placeholder="请选择">
              <el-option
                v-for="(directory, index) in twoLevelDirectoryList"
                :key="index"
                :label="directory.label"
                :value="directory.value"
              ></el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item label="标签" prop="tags">
            <el-select v-model="form.tags" placeholder="请选择">
              <el-option
                v-for="(tag, index) in tagsList"
                :key="index"
                :label="tag.label"
                :value="tag.value"
              ></el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item label="关键字" prop="keyword">
            <el-input
              v-model="form.keyword"
              placeholder="根据题干搜索"
            ></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item label="试题类型" prop="questionType">
            <el-select v-model="form.questionType" placeholder="请选择">
              <el-option label="单选" value="1"></el-option>
              <el-option label="多选" value="2"></el-option>
              <el-option label="简单" value="3"></el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item label="难度" prop="difficulty">
            <el-select v-model="form.difficulty" placeholder="请选择">
              <el-option label="简单" value="1"></el-option>
              <el-option label="一般" value="2"></el-option>
              <el-option label="困难" value="3"></el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item label="方向" prop="direction">
            <el-select v-model="form.direction" placeholder="请选择">
              <el-option label="o2o" value="o2o"></el-option>
              <el-option label="外包服务" value="外包服务"></el-option>
              <el-option label="企业服务" value="企业服务"></el-option>
              <el-option label="互联网金融" value="互联网金融"></el-option>
              <el-option label="企业咨询" value="企业咨询"></el-option>
              <el-option label="互联网" value="互联网"></el-option>
              <el-option label="电子商务" value="电子商务"></el-option>
              <el-option label="其他" value="其他"></el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item label="录入人" prop="creatorID">
            <el-select v-model="form.creatorID" placeholder="请选择">
              <el-option
                v-for="user in users"
                :key="user.id"
                :label="user.username"
                :value="user.id"
              ></el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item label="题目备注" prop="remarks">
            <el-input v-model="form.remarks"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item label="企业简称" prop="shortName">
            <el-input v-model="form.shortName"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item label="城市" prop="province" class="city">
            <el-select v-model="form.province" placeholder="请选择">
              <el-option
                v-for="(city, index) in cityList"
                :key="index"
                :label="city"
                :value="city"
              ></el-option>
            </el-select>
            <el-select v-model="form.city" placeholder="请选择">
              <el-option
                v-for="(area, index) in areaList"
                :key="index"
                :label="area"
                :value="area"
              ></el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="6" class="manipulate">
          <el-button size="small" @click="$refs.form.resetFields()">
            清除
          </el-button>
          <el-button size="small" type="primary" @click="onSubmit">
            搜索
          </el-button>
        </el-col>
      </el-row>
    </el-form>
  </div>
</template>

<script>
import { provinces, citys } from '@/api/hmmm/citys.js'
import { createNamespacedHelpers, mapGetters } from 'vuex'
const { mapActions } = createNamespacedHelpers('questions')

export default {
  data() {
    return {
      form: {
        subjectID: '',
        catalogID: '',
        tags: '',
        keyword: '',
        questionType: '',
        difficulty: '',
        direction: '',
        creatorID: '',
        remarks: '',
        shortName: '',
        province: '',
        city: ''
      },
      cityList: [],
      areaList: []
    }
  },
  computed: {
    ...mapGetters(['subjectList', 'twoLevelDirectoryList', 'tagsList', 'users'])
  },
  methods: {
    ...mapActions([
      'UpdateSubjectList',
      'UpdateTwoLevelDirectoryList',
      'UpdateTagsList',
      'UpdateUsers'
    ]),

    onSubmit() {
      // console.log(this.form)
      this.$emit('submit', this.form)
    }
  },
  async created() {
    this.cityList = provinces()
    // console.log(this.cityList)
    await this.UpdateSubjectList()
    // console.log(this.subjectList)
    await this.UpdateUsers()
    // console.log(this.users)
  },
  watch: {
    async 'form.subjectID'() {
      // console.log(this.form.subjectID)
      await this.UpdateTwoLevelDirectoryList({ subjectID: this.form.subjectID })
      await this.UpdateTagsList({ subjectID: this.form.subjectID })
    },
    'form.province'() {
      this.areaList = citys(this.form.province)
      // console.log(this.areaList)
    }
  }
}
</script>

<style scoped lang="less">
.questions_select_body {
  .btn_wrapper {
    display: flex;
    margin-bottom: 15px;
    justify-content: space-between;
    span {
      font-size: 12px;
      color: pink;
    }
  }
  .select-form {
    .manipulate {
      text-align: right;
    }
    .el-select {
      width: 100%;
    }
    .city {
      // margin-right: 3px;
      .el-select {
        &:first-child {
          width: 48%;
          margin-right: 2%;
        }
        &:last-child {
          width: 50%;
        }
      }
    }
  }
}
</style>
